*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.react-flow__node-ConstantSource {
  width: 120px;
}
.react-flow__node-Gain {
  width: 120px;
}

.customNode {
  background: #999;
  border-radius: 4px;
  color: #fff;
  font-size: 12px;
  line-height: 10px;
}

.selected .customNode {
  background: #777;
}

.customNode_header {
  align-items: center;
  background: #444;
  border-radius: 4px 4px 0 0;
  display: flex;
  font-weight: bold;
  height: 20px;
  justify-content: center;
  padding: 5px;
}

.customNode_body {
  padding: 5px 0;
}

.customNode_body:after {
  clear: both;
  content: "";
  display: block;
}

.customNode_inputs,
.customNode_outputs {
}

.customNode_inputs {
  float: left;
}

.customNode_inputs .customNode_item {
  justify-content: flex-start;
}

.customNode_outputs {
  float: right;
}

.customNode_outputs .customNode_item {
  justify-content: flex-end;
}

.customNode_editor {
  clear: both;
  padding: 5px 0;
}

.customNode_editor input[type="text"],
.customNode_editor input[type="number"],
.customNode_editor input[type="range"] {
  height: 20px;
  margin: 0;
  position: relative;
  width: 100%;
}

.customNode_editor button {
  font-size: inherit;
}

.customNode_editor input[type="range"]:after {
  content: attr(value);
  font-size: 10px;
  font-weight: bold;
  left: 50%;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translateX(-50%) translateY(-75%);
}

.customNode_item {
  align-items: center;
  clear: both;
  display: flex;
  padding: 5px 10px;
  position: relative;
}

.customNode_item .react-flow__handle {
  border-width: 2px;
  height: 10px;
  width: 10px;
  top: 50%;
}

.customNode_item .react-flow__handle-left {
  transform: translateX(-50%) translateY(-50%);
  left: 0;
}

.customNode_item .react-flow__handle-right {
  transform: translateX(50%) translateY(-50%);
  right: 0;
}

.react-flow .react-flow__edge-path {
  stroke-width: 3;
}

.contextMenu {
  background: #eee;
  box-shadow: 2px 2px 4px #ccc;
  list-style: none;
  margin: 0;
  padding: 0;
}

.contextMenu li {
  cursor: pointer;
  padding: 4px;
}

.contextMenu li:hover {
  background: #ccc;
}
